<template>
  <div class="about-wrapper">
    <app-header title="更多" />
    <div class="content van-hairline--top">
      <img src="@assets/images/user/app-icon.png" />
      <div class="info" @click="showSubVersion">
        {{ appName }}
        <br />
        {{ appVersion }}
      </div>

      <!-- 子应用版本 -->
      <div v-show="subVisable">
        <p v-for="item in modulesList" :key="item.code">
          {{ item.name }}:{{ subVersion[item.code] }}
        </p>
        <button @click="hideSubVersion">关闭</button>
      </div>
    </div>
    <app-cell
      class="van-hairline--bottom"
      @click="showWechatQr"
      title="关注公众号"
    />

    <app-cell
      class="van-hairline--bottom"
      @click="showAppQR"
      title="二维码下载"
    >
      <span slot="right">iOS、安卓通用</span>
    </app-cell>
    <a href="tel: 0755-33004729" class="tel-box">
      <app-cell class="van-hairline--bottom" title="客服热线" :is-link="false">
        <a slot="right" class="tel" href="tel: 0755-33004729">
          0755-33004729
        </a>
      </app-cell>
    </a>

    <van-popup v-model="show" class="qr-popup">
      <div class="qr-wrapper">
        <div class="qr-content">
          <img :src="qrPhoto" width="100%" />
          {{ appName }}
          <app-button class="qr-button" @click="save">保存至相册</app-button>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import Version from '@src/common/version';
import AppButton from '@components/app-button/index.vue';
import AppCell from '@components/app-cell/index.vue';
import plugin from '@src/common/plugin';
import path from '@yl-app-jssdk/path';
import CONST from '@src/config/const';
import localStorageApi from '@yl/localstorage';
import modulesList from '@config/modules';

import AppQrPhoto from '@/public/resource/qr-app.png';
import WechatQrPhoto from '@/public/resource/qr-wechat.png';

export default {
  components: {
    AppButton,
    AppCell,
  },

  data() {
    return {
      appName: '明源智慧工程',
      appVersion: '',
      show: false,
      qrPhoto: '',
      savePhoto: '',

      modulesList, // 子应用列表
      subVersion: {}, // 子应用版本号
      subCount: 0, // 点击次数，超过5次显示子应用版本
      subVisable: false, // 是否显示子应用版本号
    };
  },

  activated() {
    const versionInfo = Version.getAppFullVersion(true);

    this.appVersion = `V${versionInfo.fullVersion}`;

    this.getSubVersion();
  },

  methods: {
    showAppQR() {
      this.qrPhoto = AppQrPhoto;
      this.savePhoto = 'resource/qr-app-save.png';
      this.show = true;
    },
    showWechatQr() {
      this.qrPhoto = WechatQrPhoto;
      this.savePhoto = 'resource/qr-wechat-save.png';
      this.show = true;
    },

    // 显示子应用版本
    showSubVersion() {
      this.subCount++;
      if (this.subCount > 5) {
        this.subVisable = true;
        this.subCount = 0;
      }
    },

    // 隐藏子应用版本展示
    hideSubVersion() {
      this.subVisable = false;
    },

    // 获取子应用版本
    getSubVersion() {
      const version = localStorageApi.getObject(CONST.VERSION);
      // 循环子应用，获取子应用版本
      Object.keys(modulesList).forEach(v => {
        this.subVersion[v] = (version && version[v]) || '暂无版本';
      });
    },

    save() {
      let qrsrc = '';
      if (process.env.VUE_APP_ENV === 'dev') {
        qrsrc = window.location.origin + '/' + this.savePhoto;
      } else {
        qrsrc = path.getWebCodeDir() + this.savePhoto;
      }
      plugin
        .savePicToAlbum(qrsrc)
        .then(() => {
          this.$toast('保存成功');
        })
        .catch(() => {
          this.$toast('保存失败，请开启打开保存权限');
        });
    },
  },
};
</script>

